.playerEdit {
    width: 60%;
    height: 75%;
    border-radius: 10px;
    position: absolute;
    top: 100px;
    left: 50%;
    transform: translate(-50%, 0%);
    bottom: 40px;
    background-color: #fff;
    font-family: 'Poppins';
    overflow-y: scroll;
    &__overlay {
        background-color: #131313;
    }
    .playerEditForm {
        &__general,
        &__personalData {
            &__header {
                display: flex;
                justify-content: space-between;

                &--iconBtn {
                    color: red;
                    font-size: 29px;
                    margin-top: 27px;
                    margin-right: 27px;
                    cursor: pointer;
                    border: none;
                    background-color: #fff;
                    padding: 0;
                }
            }
            &--input {
                display: grid;
                grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
            }
        }
        &__general {
            &--avatar,
            &--avatarOverlay {
                width: 150px;
                height: 150px;
                margin-top: 3%;
                margin-left: 43%;
                cursor: pointer;
                border-radius: 50%;
            }
            &--avatarOverlay {
                position: relative;
                background: #0000009b;
                margin-top: -156px;
                transition: 0.6s ease;
                opacity: 0;

                &:hover {
                    opacity: 1;
                }
                .editIcon {
                    padding: 70px;
                    color: #fff;
                    font-size: 30px;
                    cursor: pointer;
                    margin-left: -5px;
                }
            }
            &--uploadBtn {
                position: absolute;
                display: none;
            }
        }

        // &__stats {
        //     &--header1,
        //     &--header2 {
        //         margin-left: 2%;
        //         font-weight: 400;
        //         margin-top: 1%;
        //         color: #131313;
        //         font-size: 27px;
        //     }
        // }
        &__btn {
            display: flex;
            margin-left: 2%;
            margin-top: 2%;
            margin-bottom: 2%;
            &--cancel,
            &--submit {
                font-size: 14px;
                color: #fff;
                margin-right: 0.8rem;
                font-family: 'Poppins';
                padding: 0.4rem 1.2em;
                border: none;
                font-weight: 600;
                cursor: pointer;
                text-align: center;
                border-radius: 10px;
            }
            &--submit {
                background-color: #0d4194;
                &:hover {
                    background-color: #104aa7;
                }
            }
            &--cancel {
                background-color: #131313;
                &:hover {
                    background-color: #3a3a3a;
                }
            }
        }
    }
}
.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.541);
}
.playerStats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    background-color: #f8f8f8;
    margin: 20px;
    border-radius: 15px;
    color: #292929;
    &__battingStats,
    &__bowlingStats {
        margin-left: 70px;
        h1 {
            margin: 7px 0;
        }
    }
}
@media screen and (max-width: 799px) {
    .playerEdit {
        width: 90%;

        .playerEditForm {
            .input_box {
                max-width: 400px;
            }
            .selectInputBox {
                max-width: 400px;
            }
            &__general {
                &--avatar,
                &--avatarOverlay {
                    margin-left: 34%;
                }
            }
            &__btn {
                display: grid;
                &--submit {
                    margin-top: 4%;
                    margin-bottom: 4%;
                }
            }
        }
    }
    .playerStats {
        text-align: center;
        &__battingStats,
        &__bowlingStats {
            margin-left: 0;
        }
    }
}
